<template>
    <div>
        <TopButtons/>
        <div style="height: 10px"></div>
        <a-card style="padding-top: 20px">
            <Info :id="thisId"/>
        </a-card>
    </div>
</template>

<script>
    import TopButtons from '../会议列表/components/TopButtons'
    import {getMeeting} from '@/api/meeting'
    import {in_array, array_column} from '@/libs/tools'
    import Info from '@/components/meeting/info-list/Index'

    export default {
        name: "Index",
        components: {
            TopButtons,Info
        },
        props: {
            id: {
                required: true,
                type: [String, Number]
            },
        },
        data() {
            return {
                meeting: {},
            }
        },
        computed: {
            thisId() {
                return parseInt(this.id);
            },
            type() {
                return this.meeting.hasOwnProperty('type_format') ? this.meeting.type_format : null;
            }
        },
        methods: {
            init() {
                if (!this.thisId) {
                    history.go(-1);
                    return false;
                }

                getMeeting(this.thisId)
                    .then(res => {
                        this.meeting = res.data;
                        this.spinning = false;
                        return this.verify();
                    })
                    .catch(err => {
                        this.$message.error(err);
                        history.go(-1);
                        return false;
                    })
            },
            verify() {
                let user_id = parseInt(this.$store.state.common.user_id);
                if (!this.meeting.hasOwnProperty('id')) {
                    throw '获取会议数据失败';
                }

                if (this.meeting.attend_users.length &&
                    !in_array(user_id, array_column(this.meeting.attend_users, 'id'))) {
                    throw '您没有参会权限';
                }

                if (!(this.meeting.audit && this.meeting.audit.status_format === '预约成功')) {
                    throw '会议未审核通过';
                }
            },
        },
        mounted() {
            this.init();
        }
    }
</script>

